.hero.hero--auth {
  display: block;
  height: auto;
}
.hero.hero--auth .hero__header {
  margin: 0;
  padding: 0 1rem;
  position: relative;
  height: auto;
  max-width: 1200px;
  justify-content: flex-start;
  text-align: left;
}
.hero.hero--auth .hero__header .hero__headline {
  text-align: left;
  width: 100%;
  margin-bottom: 0;
  margin: 0 auto 4rem;
  max-width: 1280px;
}
.hero.hero--auth .hero__header .topbar.description {
  text-align: left;
  color: #fff;
  font-size: 1.25rem;
  font-weight: lighter;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  padding: 1rem 0;
}
.hero.hero--auth .hero__header .topbar-stick-top {
  position: -webkit-sticky;
  position: sticky;
  top: 75px;
  height: 0;
  width: 98vw;
  margin-left: -1rem;
  border-bottom: 1px solid #30334b;
  background: #0e133c;
  z-index: 10;
}
.hero.hero--auth .mainlayout {
  display: flex;
  flex-direction: row;
}
.hero.hero--auth .mainlayout .navigation-wrapper {
  position: sticky;
  top: 75px;
  width: 30vw;
  max-width: 30rem;
  height: calc(100vh - 105px);
  overflow: auto;
  background-color: rgba(255,255,255,0.05);
  padding: 1em;
  scrollbar-color: $navigation-scroll transparent !important;
  scrollbar-width: thin;
/* set button(top and bottom of the scrollbar) */
}
.hero.hero--auth .mainlayout .navigation-wrapper::-webkit-scrollbar {
  width: 20px;
  height: 20px;
}
.hero.hero--auth .mainlayout .navigation-wrapper::-webkit-scrollbar-track {
  box-shadow: inset 0 0 20px 20px transparent;
  border: solid 6px transparent;
}
.hero.hero--auth .mainlayout .navigation-wrapper::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 20px 20px $navigation-scroll;
  border: solid 6px transparent;
  border-radius: 20px;
}
.hero.hero--auth .mainlayout .navigation-wrapper::-webkit-scrollbar-button {
  display: none;
}
.hero.hero--auth .mainlayout .navigation-wrapper::-webkit-scrollbar-corner {
  background-color: transparent;
}
.hero.hero--auth .mainlayout .navigation-wrapper .navigation-section {
  padding-top: 0.5em;
  padding-bottom: 1em;
  color: #fff;
  cursor: default;
}
.hero.hero--auth .mainlayout .navigation-wrapper .navigation-section .header {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.hero.hero--auth .mainlayout .navigation-wrapper .navigation-section .header .navigation-headline {
  font-size: medium;
  text-transform: uppercase;
}
.hero.hero--auth .mainlayout .navigation-wrapper .navigation-section .body .navigation-link {
  margin-bottom: 1rem;
}
.hero.hero--auth .mainlayout .navigation-wrapper .navigation-section .body .navigation-link .navigation-link.tag.header {
  padding: 0.2rem 0.2rem 0.2rem 1rem;
  margin: 0 -1rem;
}
.hero.hero--auth .mainlayout .navigation-wrapper .navigation-section .body .navigation-link .navigation-link.tag.header .navigation-linktag {
  display: block;
  font-size: 1.25rem;
  font-weight: 400;
  padding: 0.25rem 0.5rem 0.25rem 0;
}
.hero.hero--auth .mainlayout .navigation-wrapper .navigation-section:not(:last-child) {
  border-bottom: 1px solid #25294a;
}
.hero.hero--auth .mainlayout .main {
  background-color: #0a0c23;
  color: #fff;
  min-height: 100vh;
  width: 70vw;
  padding: 0 3em 10rem;
}
@media (min-width: 100rem) {
  .hero.hero--auth .mainlayout .main {
    width: calc(100vw - 30rem);
  }
}
