}
.token.atrule,
.token.attr-value,
.token.hex,
.token.string {
  color: #b0c975;
}
.token.property,
.token.entity,
.token.url,
.token.attr-name,
.token.keyword {
  color: #c27628;
}
.token.regex {
  color: #9b71c6;
}
.token.entity {
  cursor: help;
}
.token.function,
.token.constant {
  color: #e5a638;
}
.token.variable {
  color: #fdfba8;
}
.token.number {
  color: #8799b0;
}
.token.important,
.token.deliminator {
  color: #e45734;
}
pre[data-line] {
  position: relative;
  padding: 1em 0 1em 3em;
}
.line-highlight {
  position: absolute;
  left: 0;
  right: 0;
  margin-top: 1em; /* Same as .prism's padding-top */
  background: rgba(255,255,255,0.2);
  pointer-events: none;
  line-height: inherit;
  white-space: pre;
}
.line-highlight:before,
.line-highlight[data-end]:after {
  content: attr(data-start);
  position: absolute;
  top: 0.3em;
  left: 0.6em;
  min-width: 1em;
  padding: 0 0.5em;
  background-color: rgba(255,255,255,0.3);
  color: #fff;
  font: bold 65%/1.5 sans-serif;
  text-align: center;
  -moz-border-radius: 8px;
  -webkit-border-radius: 8px;
  border-radius: 8px;
  text-shadow: none;
}
.line-highlight[data-end]:after {
  content: attr(data-end);
  top: auto;
  bottom: 0.4em;
}
.line-numbers-rows {
  margin: 0;
}
.line-numbers-rows span {
  padding-right: 10px;
  border-right: 3px #d9d336 solid;
}
.topbar {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: flex-end;
  background: #0c0f2c;
  background: linear-gradient(0deg, #0e133c 100%, #0c0f2c 0%);
  z-index: 25;
}
